*{
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
body, html{
	height: 100%;
}



#root,#root > div{
	height: 100%;
	overflow: auto;
}
#root{
	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#6f3615+0,ef6337+72 */
background: #6f3615; /* Old browsers */ /* FF3.6-15 */
background: -webkit-linear-gradient(-45deg, #6f3615 0%,#ef6337 72%); /* Chrome10-25,Safari5.1-6 */
background: -webkit-linear-gradient(315deg, #6f3615 0%,#ef6337 72%);
background: -o-linear-gradient(315deg, #6f3615 0%,#ef6337 72%);
background: linear-gradient(135deg, #6f3615 0%,#ef6337 72%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#6f3615', endColorstr='#ef6337',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}
#root > div{
	max-width: 500px;
	margin: auto;
}
#root > div{
	
}
input:focus{
	outline: none;
	border-color: blue;
	-webkit-transition: all ease-out 0.3s 0s;
	-o-transition: all ease-out 0.3s 0s;
	transition: all ease-out 0.3s 0s;
}



.Checkout {
  margin: 0 auto;
  max-width: 800px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 5px;
}

button {
  white-space: nowrap;
  border: 0;
  outline: 0;
  display: inline-block;
  height: 40px;
  line-height: 40px;
  padding: 0 14px;
  -webkit-box-shadow: 0 4px 6px rgba(50, 50, 93, .11), 0 1px 3px rgba(0, 0, 0, .08);
          box-shadow: 0 4px 6px rgba(50, 50, 93, .11), 0 1px 3px rgba(0, 0, 0, .08);
  color: #fff;
  border-radius: 4px;
  font-size: 15px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.025em;
  background-color: #6772e5;
  text-decoration: none;
  -webkit-transition: all 150ms ease;
  -o-transition: all 150ms ease;
  transition: all 150ms ease;
  margin-top: 10px;
}

form {
  margin-bottom: 40px;
  padding-bottom: 40px;
  border-bottom: 3px solid #e6ebf1;
}

button:hover {
  color: #fff;
  cursor: pointer;
  background-color: #7795f8;
  -webkit-transform: translateY(-1px);
      -ms-transform: translateY(-1px);
          transform: translateY(-1px);
  -webkit-box-shadow: 0 7px 14px rgba(50, 50, 93, .10), 0 3px 6px rgba(0, 0, 0, .08);
          box-shadow: 0 7px 14px rgba(50, 50, 93, .10), 0 3px 6px rgba(0, 0, 0, .08);
}

.StripeElement {
  display: block;
  margin: 10px 0 20px 0;
  max-width: 500px;
  padding: 10px 14px;
  -webkit-box-shadow: rgba(50, 50, 93, 0.14902) 0px 1px 3px, rgba(0, 0, 0, 0.0196078) 0px 1px 0px;
          box-shadow: rgba(50, 50, 93, 0.14902) 0px 1px 3px, rgba(0, 0, 0, 0.0196078) 0px 1px 0px;
  border-radius: 4px;
  background: white;
}

.StripeElement--focus {
  -webkit-box-shadow: rgba(50, 50, 93, 0.109804) 0px 4px 6px, rgba(0, 0, 0, 0.0784314) 0px 1px 3px;
          box-shadow: rgba(50, 50, 93, 0.109804) 0px 4px 6px, rgba(0, 0, 0, 0.0784314) 0px 1px 3px;
  -webkit-transition: all 150ms ease;
  -o-transition: all 150ms ease;
  transition: all 150ms ease;
}



